.container {
  max-width: 138rem;
  margin-left: auto;
  margin-right: auto;
  padding-left: 1.6rem;
  padding-right: 1.6rem;
}

.row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(0, 1fr));
  grid-template-rows: 1fr;
  gap: 1rem;
}

@media only screen and (max-width: 860px) {
  .row {
    grid-template-columns: 1fr;
    gap: 5rem;
  }
}
.section {
  padding: 4em 0;
}

.section-title {
  margin-top: 1rem;
  margin-bottom: 8rem;
  text-align: center;
  font-size: 4rem;
  text-transform: uppercase;
  position: relative;
  padding-bottom: 2rem;
}

.section-title span {
  text-align: center;
  font-size: 2rem;
  text-transform: lowercase;
  display: block;
  font-weight: normal;
}

.section-title:after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: 0;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 12rem;
  height: 2px;
  background: #00FFE4;
}

.btn {
  text-transform: uppercase;
  font-weight: bold;
  color: #fff;
  background: #333;
  display: inline-block;
  border: 0;
  padding: 2rem 3.5rem;
  border-radius: 4rem;
  min-width: 16rem;
  transition: 0.5s all;
  cursor: pointer;
  text-decoration: none;
}

.btn:hover {
  background: #00FFE4;
  color: #333;
}

.loading {
  position: relative;
}

.loading:before {
  content: "";
  display: block;
  width: 3rem;
  height: 3rem;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-right-color: #fff;
  border-radius: 50%;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%) rotate(0deg);
          transform: translate(-50%, -50%) rotate(0deg);
  -webkit-animation: loadingAnim 0.6s 0s infinite linear;
          animation: loadingAnim 0.6s 0s infinite linear;
}

.btn.loading {
  cursor: default;
  padding-left: 5rem;
  background: #333;
  color: rgba(255, 255, 255, 0.2);
}

.btn.loading:before {
  left: 3rem;
}

*, *:before, *:after {
  box-sizing: border-box;
}

html {
  font-size: 10px;
}

@media (min-width: 1900px) {
  html {
    font-size: 11px;
  }
}
body {
  min-width: 32rem;
  font-family: "Nunito Sans", sans-serif;
  font-size: 1.6rem;
}

.page-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  background: transparent;
  transition: 0.5s all;
  padding: 1.5rem 0;
}

.page-header-logo {
  display: block;
}

.page-header-logo img {
  max-width: 12rem;
  height: auto;
  display: block;
}

.page-header.sticky {
  background: #000;
  box-shadow: 0 1px 0.8rem rgba(0, 0, 0, 0.5);
  padding: 0.5rem 0;
}

.page-header .container {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (max-width: 760px) {
  .page-header {
    position: absolute;
  }

  .page-header.sticky {
    /* tutaj już header nie jest przyklejony więc nie ma sensu go zmieniać */
    background: transparent;
    box-shadow: none;
  }

  .page-header .container {
    flex-direction: column;
  }
}
.page-nav {
  margin: 0;
  padding: 0;
  list-style: none;
}

.page-nav-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.page-nav-list li {
  display: inline-block;
}

.page-nav-list a {
  text-transform: uppercase;
  font-weight: 500;
  text-decoration: none;
  padding: 1rem 2rem;
  color: rgba(255, 255, 255, 0.8);
  position: relative;
  transition: 0.5s all;
}

.page-nav-list a:before {
  content: "";
  position: absolute;
  left: 50%;
  right: 50%;
  top: 100%;
  opacity: 0;
  height: 2px;
  background: #00FFE4;
  transition: 0.4s all ease-out;
}

.page-nav-list a:hover {
  color: #00FFE4;
}

.page-nav-list a:hover:before {
  left: 1.5rem;
  right: 1.5rem;
  top: 100%;
  opacity: 1;
}

@media (max-width: 760px) {
  .page-nav-list {
    text-align: center;
  }
}
@media (max-width: 560px) {
  .page-nav-list li {
    margin-bottom: 2.5rem;
  }
}
.main-banner {
  height: calc(100vh + 5rem);
  min-height: 800px;
  background-color: #202020;
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), linear-gradient(90deg, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.8)), url(../images/main-banner3.jpg);
  background-size: cover;
  background-repeat: repeat, repeat, no-repeat;
  background-position: center bottom;
  background-attachment: scroll, fixed, fixed;
  display: grid;
  grid-template-rows: 9.6rem 1fr 8rem 5rem;
  grid-template-areas: "banner-header" "banner-content" "banner-icon" "banner-footer";
}

@media (max-width: 760px) {
  .main-banner {
    grid-template-rows: 24rem 1fr 8rem 5rem;
  }
}
.main-banner-content {
  grid-area: banner-content;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.main-banner-title {
  width: 70%;
  max-width: 75rem;
  opacity: 0.9;
}

@media (max-width: 760px) {
  .main-banner-content {
    justify-content: flex-start;
  }
}
.main-banner-text {
  margin-top: 3.2rem;
  max-width: 100rem;
  padding-left: 1.6rem;
  padding-right: 1.6rem;
  font-size: 1.8rem;
  color: rgba(255, 255, 255, 0.35);
  text-align: center;
}

.main-banner-button {
  margin-top: 6rem;
  font-size: 1.6rem;
  font-weight: 500;
  text-decoration: none;
  text-transform: uppercase;
  color: #FFF;
  padding: 2.2rem 6rem;
  border-radius: 4.8rem;
  border: 1px solid rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.05);
  transition: 0.5s all;
  position: relative;
}

.main-banner-button:hover {
  background: rgba(0, 255, 228, 0.9);
  color: #222;
  border-color: #00FFE4;
}

.main-banner-button:after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: 100%;
  height: 100%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border: 1px solid #00FFE4;
  opacity: 0;
  border-radius: 4.8rem;
  transition: 0.5s all;
}

.main-banner-button:hover:after {
  -webkit-animation: mainBannerBtnAnim 0.5s 0s 1 ease-in;
          animation: mainBannerBtnAnim 0.5s 0s 1 ease-in;
}

@-webkit-keyframes mainBannerBtnAnim {
  10% {
    opacity: 0.7;
  }
  100% {
    opacity: 0;
    width: calc(100% + 4rem);
    height: calc(100% + 4rem);
  }
}

@keyframes mainBannerBtnAnim {
  10% {
    opacity: 0.7;
  }
  100% {
    opacity: 0;
    width: calc(100% + 4rem);
    height: calc(100% + 4rem);
  }
}
.main-banner-footer {
  grid-area: banner-footer;
  display: flex;
}

.main-banner-footer span {
  width: 26rem;
  height: 5rem;
  background: url(../images/banner-bottom.svg) no-repeat;
  background-size: 100% 100%;
  display: block;
}

.main-banner-footer:before,
.main-banner-footer:after {
  content: "";
  background: #FFF;
  flex-grow: 1;
}

.main-banner-scroll-icon {
  width: 2rem;
  height: 4rem;
  border: 1px solid rgba(255, 255, 255, 0.4);
  border-radius: 2rem;
  position: relative;
  grid-area: banner-icon;
  align-self: center;
  margin: 0 auto;
}

.main-banner-scroll-icon:before {
  width: 0.5rem;
  height: 0.8rem;
  border-radius: 0.3rem;
  background: rgba(255, 255, 255, 0.8);
  content: "";
  position: absolute;
  left: 50%;
  top: 0.5rem;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  -webkit-animation: bannerScrollIconAnim 0.6s 0s infinite ease-in-out alternate;
          animation: bannerScrollIconAnim 0.6s 0s infinite ease-in-out alternate;
}

@-webkit-keyframes bannerScrollIconAnim {
  100% {
    top: 2.4rem;
  }
}

@keyframes bannerScrollIconAnim {
  100% {
    top: 2.4rem;
  }
}
@media (max-height: 730px) {
  .main-banner-scroll-icon {
    display: none;
  }
}
.box {
  max-width: 50rem;
  margin-left: auto;
  margin-right: auto;
}

.box-photo {
  position: relative;
  display: inline-block;
  margin-bottom: 2rem;
}

.box-photo-img {
  overflow: hidden;
  display: block;
  max-width: 100%;
  height: auto;
}

.box-photo:hover .box-photo-overlay {
  opacity: 1;
}

.box-photo-overlay {
  opacity: 0;
  background: rgba(17, 17, 17, 0.8);
  transition: 0.5s all;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.box-photo-overlay:before,
.box-photo-overlay:after {
  content: "";
  width: 6rem;
  height: 1px;
  background: rgba(255, 255, 255, 0.8);
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.box-photo-overlay:after {
  -webkit-transform: translate(-50%, -50%) rotate(-90deg);
          transform: translate(-50%, -50%) rotate(-90deg);
}

.box-title {
  text-transform: uppercase;
  font-size: 1.8rem;
}

.box-content {
  margin-bottom: 4rem;
  color: #AAA;
}

