.main-parallax {
  height: 51rem;
  background-image: linear-gradient(rgba(34, 34, 34, 0.4), rgba(34, 34, 34, 0.4)), linear-gradient(90deg, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.6)), url(../images/banner-paralax.jpg);
  background-position: center center;
  background-attachment: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
